<template>
    <header id="header" class="header bg-white animated" :class="[ scrollSwitch ? 'slideUp' : 'slideDown']">
      <div class="navBar-container">
        <div class="front"><a href="#">xxxx</a></div>
        <div class="icon"><span></span></div>
        <div>
          <a class="item">xxx</a>
          <a class="item">xxx</a>
          <a class="item">xxx</a>
          <!-- <a>u趣的</a> -->
          <a class="item">contact</a>
        </div>
      </div>
    </header>
</template>

<script type="ts" setup>
import { ref } from "vue";

const scrollSwitch = ref(false);

//监听页面滚动事件
//1.向下滚动 隐藏header头
//2.向上滚动 导出header头
let windowPageYOffsetPrev;
document.addEventListener("scroll", () => {
  if (window.pageYOffset > windowPageYOffsetPrev && window.pageYOffset > 0) {
    //页面偏移量大于滚动条上次记录的位置
    scrollSwitch.value = true;
  } else {
    scrollSwitch.value = false;
  }
  windowPageYOffsetPrev = window.pageYOffset;
});
</script>

<style lang="less" >
.header {
  position: fixed;
  top: 0;
  z-index: 10;
  width: 100%;
  height: 70px;
  line-height: 72px;
  padding: 0;
  display: block;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
}

.navBar-container div {
  display: inline-block;
  position: relative;
}

.header div a {
  position: relative;
}

.header div .item:hover {
  background-color: #ffeded54;
  color: #ec5454;
}

.header div .item::before {
  content: "";
  position: absolute;
  left: 50%;
  right: 50%;
  top: 70px;
  background: #ec5454;
  height: 3px;
  transition: all 0.2s;
  opacity: 0;
  -webkit-transition-property: "left, right";
  -o-transition-property: "left, right";
  transition-property: "left, right";
  -webkit-transition-duration: 0.3s;
  -o-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-timing-function: ease-out;
  -o-transition-timing-function: ease-out;
  transition-timing-function: ease-out;
}

.header div .item:hover::before {
  opacity: 1;
  left: 0;
  right: 0;
}

.navBar-container {
  position: relative;
  width: 1040px;
  max-width: 100%;
  height: 70px;
  margin: 0 auto;
}

.navBar-container .icon {
  display: none;
  top: 50%;
  margin-top: -9px;
}

.navBar-container .icon span,
.navBar-container .icon::after,
.navBar-container .icon::before {
  content: "";
  margin: 3px 0;
  display: block;
  width: 20px;
  background: #313131;
  height: 2px;
}

.navBar-container div:last-child,
.navBar-container .icon {
  position: absolute;
  right: 10px;
}

.navBar-container div:last-child a {
  font-size: 14px;
  padding: 30px 15px;
  transition-duration: 0.3s;
  text-decoration: none;
  color: #313131;
  outline: 0;
}

.header div.front {
  font-size: 22px;
  line-height: 22px;
  position: absolute;
  top: 50%;
  left: 0;
  display: block;
  width: auto;
  max-width: 50%;
  height: 22px;
  margin-top: -10px;
  margin-left: 20px;
  text-decoration: none;
}

.header.animated {
  animation-duration: 0.5s;
  animation-fill-mode: both;
}

.header.animated.slideDown {
  animation-name: slideDown;
}

.header.animated.slideUp {
  animation-name: slideUp;
}

@keyframes slideDown {
  0% {
    transform: translateY(-70px);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes slideUp {
  0% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(-70px);
  }
}

.bg-white {
  background-color: rgba(255, 255, 255, 0.7);
  -webkit-backdrop-filter: blur(20px);
  backdrop-filter: blur(20px);
}

@media screen and (max-width: 700px) {
  .navBar-container div:last-child {
    display: none;
  }

  .navBar-container .icon {
    display: inline-block;
    right: 30px;
  }
}
</style>